import React, { Component } from 'react';
import './style.css';
import axios from 'axios';
import mdbostrap from 'axios';
import ChartPie from "../../Component/pie";
import ChartBar from "../../Component/bar";
import ChartLine from "../../Component/line";
import 'bootstrap/dist/css/bootstrap.min.css';

class Main extends Component {
    render() {
        return (
            <div className="page">
                <div className="container">
                    <div className="row" id="page-1">
                        <div className="elements col-lg-12">
                            <h1>Page One</h1>
                        </div>
                        <div className="elements col-lg-6">
                            <div className="card">
                                <ChartBar />
                            </div>
                        </div>
                        <div className="elements col-lg-6">
                            <div className="card">
                                <ChartPie />
                            </div>
                        </div>
                    </div>
                    <div className="row" id="page-2">
                        <div className="elements col-lg-12">
                            <h1>Page Two</h1>
                        </div>
                        <div className=" col-lg-3">
                        </div>
                        <div className="elements col-lg-6">
                            <div className="card">
                                <ChartLine />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



export default Main;