import React from 'react';
import { Link } from 'react-router-dom';
import "./style.css";
import logo from '../../logo.png';

window.onload = function load(side) {
    var side = document.getElementById("page-2");
    side.style.cssText = "display:none";
}

function handleClick(e) {
    e.preventDefault();
    var sideNav = document.getElementById("page-1");
    sideNav.style.cssText = "display:flex";
    var sideNav2 = document.getElementById("page-2");
    sideNav2.style.cssText = "display:none";
}

function handleClick2(e) {
    e.preventDefault();
    var sideNav1 = document.getElementById("page-1");
    sideNav1.style.cssText = "display:none";
    var sideNav21 = document.getElementById("page-2");
    sideNav21.style.cssText = "display:flex";
}
const Menu = () => (


    <div className="menu">
        <nav>
            <ul>
                <li>
                    <img src={logo} className="App-logo" alt="logo" />
                </li>
                <li>
                    <a href="#one" onClick={handleClick}>Page 1</a>
                </li>
                <li>
                    <a href="/two" onClick={handleClick2}>Page 2</a>
                </li>
            </ul>
        </nav>
    </div>
);


export default Menu;